<template>
    <div class="nav container">
    <h1 class="nav__left"><span>M.M.M </span> AVTO</h1>
    <div class="nav__right">
      <router-link class="nav__right-account" to="/login">Sign In</router-link>
      <router-link class="nav__right-account" to="/registir"
        >Sign Up</router-link
      >
      <router-link class="nav__right-comment" to="/komment">
        <img src="../assets/imgs/comment.png" alt="" @click="comment" />
      </router-link>
      <p @click="close">X</p>
    </div>
    <img
      @click="menu"
      class="nav-menu"
      src="../assets/imgs/menu-svgrepo-com.svg"
      alt=""
    />
  </div> 
  
</template>
<script>
export default {
  props: {
    one: Array,
  },
  data() {
    return {
      title: "",
    };
  },
  methods: {
    love() {
      let card = document.querySelector(".nav__right-card");
      card.classList.add("active");
    },
    exs() {
      let card = document.querySelector(".nav__right-card");
      card.classList.remove("active");
    },
    menu() {
      let right = document.querySelector(".nav__right");
      let left = document.querySelector(".nav__left");
      let navmenu = document.querySelector(".nav-menu");
      navmenu.classList.add("active")
      left.classList.add("active")
      right.classList.add("active")
    },
    close() {
      let right = document.querySelector(".nav__right");
      let left = document.querySelector(".nav__left");
      let navmenu = document.querySelector(".nav-menu");
      navmenu.classList.remove("active")
      left.classList.remove("active")
      right.classList.remove("active")
    },
  },
};
</script>
<style lang="scss">
.nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 125px;
  align-items: start;
  margin-top: 40px;
