      }
      &-comment {
        img {
          width: 40px;
          height: 40px;
        }
      }
    }
  }
}
@media (max-width: 650px) {
  .nav {
    display: flex;
    align-items: center;
    &__left {
      display: flex;
      align-items: center;
      gap: 20px;
      font-size: 25px;
      span {
        color: darkblue;
      }
    }
    &__right {
      gap: 20px;
      &-account {
        background: #0fafe9;
        width: 120px;
      }
      &-comment {
        img {
          width: 40px;
          height: 40px;
        }
      }
    }
  }
}
@media (max-width: 550px) {
  .nav {
    display: flex;
    align-items: start;
    position: relative;
    &__left {
      display: flex;
      align-items: center;
