<template>
  <div class="komment container">
    <div class="komment__right">
      <div class="komment__right__nav">
       <div class="komment__right__nav-top">
        <h1>SAYTIMIZ HAQIDA FIKR MULOHAZALARINGIZ UCHUN TASHAKKUR</h1>
       </div>
        <p class="komment__right__nav-text" v-for="(node, i) in comments" :key="i">
          {{ node.comment }}
        </p>
      </div>

      <div class="komment__right-footer">
        <form @submit.prevent="addCom">
          <input type="text" placeholder="muzaffar" v-model="title" />
          <button>
            <img
              v-if="title.length == 0"
              src="../assets/imgs/kamera.svg"
              alt=""
            />
            <img v-else src="../assets/imgs/yuborish.svg" alt="" />
          </button>
        </form>
      </div>
    </div>
  </div>
