<script>
import { mapActions, mapState } from "vuex";
export default {
  props: {},
  data() {
    return {
      title: "",
      
    };
  },
  computed: {
    ...mapState(["comments"]),
    ...mapState(["names"])
  },
  methods: {
    ...mapActions(["addUsers"]),

    addCom() {
     if (this.title.length != 0) {
      this.addUsers(this.title)
      this.title = "";
     }
    },
    menu(){
      let left = document.querySelector(".komment__left");
      let p = document.querySelector(".komment__left-top p");
      let menyu = document.querySelector(".komment-menu");
      menyu.classList.add("active")
      p.classList.remove("active")
      left.classList.add("active") 
    },
    close(){
      let left = document.querySelector(".komment__left");
      let p = document.querySelector(".komment__left-top p");
      let menyu = document.querySelector(".komment-menu");
      p.classList.add("active")
      menyu.classList.remove("active")
      left.classList.remove("active") 
    },
  },
};
</script>
