import { createStore } from 'vuex'
import axios from 'axios'


export default createStore({
  state: {
    infos: [],
    infos2: [],
    infos3: [],
    infos4: [],
    infos5: [],
    infos6: [],
    infos7: [],
    comments: [],
    names: [],
    emailreg: [],
    pasreg: [],
  },
  getters: {
  },
  mutations: {
    infosAdd(state, payload) {
      state.infos = payload
    },
    infosAdd2(state, payload) {
      state.infos2 = payload
    },
    infosAdd3(state, payload) {
      state.infos3 = payload
    },
    infosAdd4(state, payload) {
      state.infos4 = payload
    },
    infosAdd5(state, payload) {
      state.infos5 = payload
    },
    infosAdd6(state, payload) {
      state.infos6 = payload
    },
    infosAdd7(state, payload) {
      state.infos7 = payload
    },
    commentAdd(state, payload) {
      state.comments = payload
    },
    akkaundAdd(state, payload) {
      state.names = payload
    },
    EmailAdd(state, payload) {
      state.emailreg = payload
    },
    PaswordAdd(state, payload) {
      state.pasreg = payload
    },
  },
  actions: {
    async getUsers({ state, commit }) {
      const infos = await axios.get('https://63fa073c897af748dcc7e62a.mockapi.io/oficeOne')
      commit("infosAdd", infos.data[0].users)
      commit("infosAdd2", infos.data[1].users2)
      commit("infosAdd3", infos.data[2].users3)
      commit("infosAdd4", infos.data[3].users4)
      commit("infosAdd5", infos.data[4].users5)
      commit("infosAdd6", infos.data[5].users6)
      commit("infosAdd7", infos.data[6].users7)
    },
    async addUsers({state}, title){
      try {
        await axios.post('https://63fa073c897af748dcc7e62a.mockapi.io/comments',{comment:title})     
      } catch (error) {
        console.log(error);
      }
    },
    async getComments({ state, commit }) { 
      const users = await axios.get('https://63fa073c897af748dcc7e62a.mockapi.io/comments')
      commit("commentAdd", users.data)
    },
    async Usernames({state}, user){
      try {
        await axios.post('https://64788799362560649a2df6c8.mockapi.io/online__magazen',{name:user})     
      } catch (error) {
        console.log(error);
      }
    },
    async Akkaunt({ state, commit }) { 
      const akk = await axios.get('https://64788799362560649a2df6c8.mockapi.io/online__magazen')
      commit("akkaundAdd", akk.data)
      console.log(akk.data);
    },
    async gmailUsers({state}, email){
      try {
        await axios.post('https://64788799362560649a2df6c8.mockapi.io/signin',{gmail:email})     
      } catch (error) {
        console.log(error);
      }
    },
    async getEmail({ state, commit }) { 
      const email = await axios.get('https://64788799362560649a2df6c8.mockapi.io/signin')
      commit("EmailAdd", email.data)
    },
    async passwordUsers({state}, paswordd){
      try {
        await axios.post('https://64788799362560649a2df6c8.mockapi.io/signin',{password:paswordd})     
      } catch (error) {
        console.log(error);
      }
    },
    async getPasword({ state, commit }) { 
      const paslog = await axios.get('https://64788799362560649a2df6c8.mockapi.io/signin')
      commit("PaswordAdd", paslog.data)
    },
  },
  
})
