<template>
  <div class="dashbord container">
    <div class="dashbord">
      <div class="dashbord-one">
        <form @submit.prevent="tolov">
          <input type="text" placeholder="Qabul qiluvchining karta raqami" v-model="utkazma">
          <input type="text" placeholder="Summa" v-model="summa">
          <input type="text" placeholder="Sharh" v-model="sharh">
          <button @click="otpravit">Davom ettirish</button>
        </form>
      </div>
        <div class="dashbord-twoo">
          <form @submit.prevent="info">
            <input  type="text" placeholder="Ism" v-model="ism">
            <input  type="text" placeholder="Familia" v-model="familia">
            <input  type="text" placeholder="Otasining ismi" v-model="ochistva">
            <input  type="text" placeholder="Telefon raqami" v-model="number">
            <input  type="text" placeholder="Yashash joyi" v-model="ish">
          </form>
          <p class="dashbord-twoo-ok" @click="send">sending</p>
        </div>
        <div class="dashbord-thre">
          <div class="dashbord-thre-card">
            <div class="dashbord-thre-card-circle">
            </div>
          </div>
        </div>
        <div class="dashbord-for">
          <h2>To'lov muvaffaqiyatli amalga oshirildi</h2>
          <p>Sizga xizmat ko'rsatganimizdan mamnunmiz</p>
        </div>
      </div>
  </div>
</template>
<script>

export default {
  data() {
    return {
      ism:"",
      familia:"",
      ochistva:"",
      number:"",
      ish:"",
      utkazma:"",
      summa:"",
      sharh:"",
    }
  },

  methods: {
    send(){
      if (this.ism.length !=0 && this.familia.length !=0 && this.ochistva.length !=0 && this.number.length >= 12 && this.number != isNaN && this.ish.length !=0) {
        let one = document.querySelector(".dashbord-one");
        let twoo = document.querySelector(".dashbord-twoo");
        twoo.classList.add("active")
        one.classList.add("active")
      }
    },
    otpravit(){
      if (this.utkazma.length == 16 && this.summa >= 5000 && this.sharh != "") {
        let thre = document.querySelector(".dashbord-thre");
        let forr = document.querySelector(".dashbord-for");
        let one = document.querySelector(".dashbord-one");
        one.classList.remove("active")
        thre.classList.add("active")
        setTimeout(() => {
        thre.classList.remove("active")
        forr.classList.add("active")
        }, 5000);
      }
    }
  },
  
};
</script>
<style lang="scss">
.dashbord {
  display: flex;
  flex-direction: column;
    &-one {
      padding: 20px;
     background: white;
     max-width: 500px;
     width: 100%;
     margin-left: auto;
     margin-right: auto;
     border-radius: 10px;
     height: 300px;
      display: none;
      &.active{
        display: block;
      }
      form{
        display: flex;
        flex-direction: column;
       gap: 40px;
        align-items: center;
        input{
          border-bottom: 2px solid rgba(128, 128, 128, 0.692);
          width: 100%;
          border-radius: 5px;
          padding: 5px 15px;
          color: black;
          font-size: 18px;
        }
        button{
          display: flex;
          margin-top: 10px;
          font-weight: 600;
          font-family: Verdana, Geneva, sans-serif;
          background: darkblue;
          padding: 5px 10px;
          color: white;
          border-radius: 5px;
          font-size: 18px;
        }
      }
      
  }
  &-twoo {
    &.active{
      display: none;
    }
      form{
        border: 1px solid #ebebeb;
        padding: 20px 10px;
        background: white;
        display: flex;
        flex-direction: column;
        gap: 50px;
        align-items: center;
        justify-content: space-between;
        border-radius: 30px 0px 0 0;
        input{
          color: black;
          background: #e5e5e5;
          font-size: 18px;
          text-transform: capitalize;
          width: 100%;
          padding: 10px;
          border-radius: 5px;
        }
        input::placeholder{
          color: black;

        }
      
      }

      &-ok{
        background: green;
        width: 100%;
        display: flex;
        text-transform: uppercase;
        padding: 5px 0;
        justify-content: center;
        align-items: center;
        font-family: Verdana, Geneva, sans-serif;
        font-weight: 800;
        color: red;
        font-size: 25px;
        margin-left: auto;
        border-radius: 0 0 30px 0;
        margin-right: auto;
      }
    }
    &-thre{
      justify-content: center;
      display: none;
      &.active{
        display: flex;
      }
      &-card{
        max-width: 300px;
        width: 100%;
    height: 300px;
    border-radius: 50%;
    position: relative;
    animation: load 1s linear infinite;
    @keyframes load {
      0%{
        transform: rotate(0);
    }100%{
        transform: rotate(360deg);
    }
    }
    &::after{
      content: "";
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    box-shadow: 0 0 10px white;
   
    }
    &::before{
      content: '';
    width: 50%;
    height: 100%;
    position: absolute;
    background-image: linear-gradient(to bottom, aqua, transparent);
    border-radius: 150px 0 0 150px;
    background-size: 250px 250px;
    background-repeat: no-repeat;
    animation: rgb 5s linear infinite;
    }
    &-circle{
      width: 240px;
    height: 240px;
    background-color: #e5e5e5;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    }
      }
    }
    &-for{
      background: white;
      flex-direction: column;
      gap: 50px;
      justify-content: center;
      align-items: center;
      max-width: 500px;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
      border-radius: 10px;
      height: 300px;
      display: none;
      &.active{
        display: flex;
      }
      h2{
        font-size: 22px;
        font-weight: 800;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        text-transform: uppercase;
        max-width: 350px;
        color: black;
        width: 100%;
        text-align: center;
      }
      p{
        display: flex;
        margin-left: auto;
        margin-right: 20px;
        font-size: 18px;
        font-family: cursive;
        text-shadow: 3px 8px 2px grey;
      }
    }
}
@media (max-width:400px) {
  .dashbord{
    &-for{
      h2{
        font-size: 18px;
      }
      p{
        font-size: 16px;
        text-align: center;
      }
    }
  }
}
</style>