<template>
  <div class="registir">
    <p class="registir-text">Welcome to my Web Site! Let’s begin the adventure</p>
    <div class="registir__one">
      <p>Enter your email*</p>
      <form @submit.prevent="emaill">
        <p >
          <img src="../assets/imgs/ptichka.png" alt="">
          {{ email }} 
        </p>
        <input type="text" v-model="email">
        <button >Continue</button>
      </form>
    </div>
    <div class="registir__twoo">
      <p>Create a password*</p>
      <form @submit.prevent="pasword" >
        <p>
          <img src="../assets/imgs/ptichka.png" alt="">
          {{ paswordd }}
        </p>
        <input type="text" v-model="paswordd">
        <button>Continue</button>
      </form>
    </div>
    <div class="registir__thre">
      <p>Enter a username*</p>
      <form @submit.prevent="username">
        <p>
          <img src="../assets/imgs/ptichka.png" alt="">
          {{ user }}
        </p>
        <input type="text" v-model="user">
        <button>Continue</button>
      </form>
    </div>
    <div class="registir__for">
      <router-link @click="addcom" class="registir__for-log" to="/dashbord">sign up</router-link>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      email:"",
      paswordd:"",
      user:"",
    }
  },
  computed:{
    
  },
  methods: {
    ...mapActions(["gmailUsers"]),
    ...mapActions(["passwordUsers"]),
    ...mapActions(["Usernames"]),

    addcom(){
      if (this.user.length != 0) {
      this.Usernames(this.user)
      this.gmailUsers(this.email)
      this.passwordUsers(this.paswordd)
      this.user = "";
      this.email = "";
      this.paswordd = "";
     }
    },
    emaill(){
      let one = document.querySelector(".registir__one")
      let twoo = document.querySelector(".registir__twoo")
      let thre = document.querySelector(".registir__thre")
      let text1 = document.querySelector(".registir__one form p")
      let inp1 = document.querySelector(".registir__one form input")
      let btn1 = document.querySelector(".registir__one form button")
      if (this.email.endsWith("@gmail.com")) {
      inp1.classList.add("active");
      btn1.classList.add("active");
      text1.classList.add("active");
      twoo.classList.add("active");
    }
    },
    pasword(){
      let one = document.querySelector(".registir__one")
      let twoo = document.querySelector(".registir__twoo")
      let thre = document.querySelector(".registir__thre")
      let text2 = document.querySelector(".registir__twoo form p")
      let inp2 = document.querySelector(".registir__twoo form input")
      let btn2 = document.querySelector(".registir__twoo form button")
      if (this.paswordd.length >= 8) {
        inp2.classList.add("active")
      btn2.classList.add("active")
      text2.classList.add("active")
      thre.classList.add("active")
      }
    },
    username(){
     
      let one = document.querySelector(".registir__one")
      let twoo = document.querySelector(".registir__twoo")
      let thre = document.querySelector(".registir__thre")
      let text3 = document.querySelector(".registir__thre form p")
      let inp3 = document.querySelector(".registir__thre form input")
      let btn3 = document.querySelector(".registir__thre form button")
      let rout = document.querySelector(".registir__for-log")
      
      if (this.user.length != 0) {
        inp3.classList.add("active")
      btn3.classList.add("active")
      text3.classList.add("active")
      rout.classList.add("active")
      }
      
    },
  },
};
</script>
