<template>
    <div class="wire container">
    <div class="wire__top">
      <div class="wire__top-left" >
        <div
          class="wire__top-left-card"
          v-for="(item, id) in infos2"
          :key="id"
        >
          <img :src="item.img" alt="" />
          <div class="wire__top-left-card-box">
            <div class="wire__top-left-card-box-one">
              <p @click="naxt">{{ item.naxt }}</p>
              <p @click="kredit">{{ item.kredit }}</p>
            </div>
            <div class="wire__top-left-card-box-twoo">
              <h2>{{ item.price }}</h2>
              <h2>{{ item.doc }}</h2>
            </div>
            <div class="wire__top-left-card-box-thre">
              <h2 @click="oneyear">{{ item.oneyear }}</h2>
              <h2 @click="twooyears">{{ item.threyears }}</h2>
              <h2 @click="fiveyears">{{ item.fiveyears }}</h2>
            </div>
            <div class="wire__top-left-card-box-for">
              <h2>{{ item.summ }}</h2>
              <h2>{{ item.totalsumm1 }}</h2>
            </div>
            <div class="wire__top-left-card-box-five">
              <h2>{{ item.summ }}</h2>
              <h2>{{ item.totalsumm2 }}</h2>
            </div>
            <div class="wire__top-left-card-box-six">
              <h2>{{ item.summ }}</h2>
              <h2>{{ item.totalsumm3 }}</h2>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div> 
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      img: false,
    };
  },
  computed: {
    ...mapState(["infos2"]),
  },
  methods: {
    naxt() {
     let twoo = document.querySelectorAll(".wire__top-left-card-box-twoo");
     let thre = document.querySelectorAll(".wire__top-left-card-box-thre");
     let forr = document.querySelectorAll(".wire__top-left-card-box-for");
     let five = document.querySelectorAll(".wire__top-left-card-box-five");
     let six = document.querySelectorAll(".wire__top-left-card-box-six");
     thre.forEach(el3 => {
      el3.classList.remove("active")
     });
     forr.forEach(el4 => {
      el4.classList.remove("active")
     });
     five.forEach(el5 => {
      el5.classList.remove("active")
     });
     six.forEach(el6 => {
      el6.classList.remove("active")
     });
     twoo.forEach(el2 => {
      el2.classList.add("active")
     });
    },
    kredit() {
      let twoo = document.querySelectorAll(".wire__top-left-card-box-twoo");
     let thre = document.querySelectorAll(".wire__top-left-card-box-thre");
     let forr = document.querySelectorAll(".wire__top-left-card-box-for");
     let five = document.querySelectorAll(".wire__top-left-card-box-five");
     let six = document.querySelectorAll(".wire__top-left-card-box-six");
     twoo.forEach(el2 => {
      el2.classList.remove("active")
     });
     forr.forEach(el4 => {
      el4.classList.remove("active")
     });
     five.forEach(el5 => {
      el5.classList.remove("active")
     });
     six.forEach(el6 => {
      el6.classList.remove("active")
     });
     thre.forEach(el3 => {
      el3.classList.add("active")
     });
    },
    oneyear() {
      let twoo = document.querySelectorAll(".wire__top-left-card-box-twoo");
     let thre = document.querySelectorAll(".wire__top-left-card-box-thre");
     let forr = document.querySelectorAll(".wire__top-left-card-box-for");
     let five = document.querySelectorAll(".wire__top-left-card-box-five");
     let six = document.querySelectorAll(".wire__top-left-card-box-six");
     six.forEach(el6 => {
      el6.classList.remove("active")
     });
     five.forEach(el5 => {
      el5.classList.remove("active")
     });
     forr.forEach(el4 => {
      el4.classList.add("active")
     });
    },
    twooyears() {
      let twoo = document.querySelectorAll(".wire__top-left-card-box-twoo");
     let thre = document.querySelectorAll(".wire__top-left-card-box-thre");
     let forr = document.querySelectorAll(".wire__top-left-card-box-for");
     let five = document.querySelectorAll(".wire__top-left-card-box-five");
     let six = document.querySelectorAll(".wire__top-left-card-box-six");
     forr.forEach(el4 => {
      el4.classList.remove("active")
     });
     six.forEach(el6 => {
      el6.classList.remove("active")
     });
     five.forEach(el5 => {
      el5.classList.add("active")
     });
    },
    fiveyears() {
      let twoo = document.querySelectorAll(".wire__top-left-card-box-twoo");
     let thre = document.querySelectorAll(".wire__top-left-card-box-thre");
     let forr = document.querySelectorAll(".wire__top-left-card-box-for");
     let five = document.querySelectorAll(".wire__top-left-card-box-five");
     let six = document.querySelectorAll(".wire__top-left-card-box-six");
     forr.forEach(el4 => {
      el4.classList.remove("active")
     });
     five.forEach(el5 => {
      el5.classList.remove("active")
     });
     six.forEach(el6 => {
      el6.classList.add("active")
     });
     
    },
  },
};
</script>
